.holder {
  height: 70vw;
  width: 90vw;
  max-width: 1300px;
  max-height: 700px;
  background-color: aliceblue;
  background-image: url("../src/components/images/background_light.jpeg");
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding: 20px 0px;
}

.taskbar {
  width: 102%;
  position: absolute;
  bottom: 0px;
  height: 37px;
  background-color: white;
  opacity: 0.8;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 20;
}

.taskbar-btns {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  background-color: white;
  padding: 2px 0px;
}
.taskbar-btns > button {
  background-color: white;
  width: 22px;
  height: 22px;
  margin: 0px 8px;
  opacity: 1;
  border: 0px;
  outline: 0px;
  padding: 1px;
}
/* Button background icon */
#start {
  background-image: url("../src/components/images/start.png");
}
#store {
  background-image: url("../src/components/images/microsoft.png");
}
#explorer {
  background-image: url("../src/components/images/files.png");
}
#mails {
  background-image: url("../src/components/images/mails.png");
}
#notepad {
  background-image: url("../src/components/images/notepad.ico");
}
#chrome {
  background-image: url("../src/components/images/anime.jpg");
}
#calculator {
  background-image: url("../src/components/images/calculator.png");
}
#vscode {
  background-image: url("../src/components/images/kolorav.png");
}
#pylane {
  background-image: url("../src/components/images/python.png");
}

.running_app {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-items: center;
  transition-duration: 0.4s;
  position: absolute;
  top: 120%;
  opacity: 0.97;
}

.all_app {
  width: 75%;
  height: 75%;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  transition-duration: 0.35s;
  border-radius: 4px;
}
#vscode-app {
  opacity: 0.99;
}
.app-holder-center {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.calc {
  position: absolute;
  right: 10px;
  bottom: -500px;
  transition-duration: 0.4s;
}

.taskbar-right {
  position: absolute;
  right: 20px;
  display: flex;
  align-items: center;
  width: fit-content;
  height: 100%;
}

.util {
  height: 100%;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#battery,
#wifi,
#sidebar-icon {
  background-position: center;
  background-size: cover;
  width: 25px;
  height: 25px;
  margin: 0px 5px;
}
#battery {
  background-image: url("./components/images/battery.jpg");
}
#wifi {
  height: 23px;
  background-image: url("./components/images/wifi.jpg");
}
.wifi-details,
.battery-details {
  position: absolute;
  bottom: -500px;
  height: 120px;
  width: 200px;
  background-color: whitesmoke;
  transition-duration: 0.2s;
  margin-left: -100px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

#sidebar-icon {
  background-image: url("./components/images/sidebaricon.png");
  height: 22px;
  width: 22px;
}
